import * as Yup from 'yup';

export const validationPatientPageScheme = Yup.object().shape({
  firstName: Yup.string()
    .required(' `')
    .max(64, '    64 '),
  lastName: Yup.string()
    .required(' `')
    .max(64, '    64 '),
  patronymic: Yup.string().max(64, '-    64 '),
  phones: Yup.array().of(
    Yup.string()
      .required(' `')
      .max(13, '    13 ')
  ),
  contactMethods: Yup.array().of(
    Yup.string().oneOf(
      ['chat', 'telegramBot'],
      '   ""  "-"'
    )
  ),
});
